@import url(https://fonts.googleapis.com/css?family=Alegreya+Sans+SC:700,900|Alegreya+Sans:500,700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

html {
  text-rendering: optimizeLegibility; }

main {
  background-color: #23090f;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; }

body {
  font-family: "Alegreya Sans", sans-serif;
  background-color: #23090f; }

h1,
h2 {
  font-family: "Alegreya Sans SC", sans-serif;
  font-size: 2.5rem;
  text-align: center; }

h2 {
  font-size: 2rem; }

button {
  height: 42px;
  padding: 0 20px;
  cursor: pointer;
  border-radius: 4px;
  letter-spacing: 0.02rem;
  font-family: "Alegreya Sans", sans-serif;
  font-size: 1rem;
  font-weight: bold; }
  button.primary-button {
    background-color: #f9f2e6;
    border: 1px solid #f9f2e6;
    text-transform: uppercase;
    transition: 0.3s; }
    button.primary-button:hover {
      color: #fff;
      background-color: #f0938c;
      transition: 0.3s; }

.container {
  max-width: 1200px;
  width: 100%;
  min-height: 100vh;
  min-height: calc(1vh * 100);
  min-height: calc(var(--vh, 1vh) * 100); }

.toastify {
  font-family: "Alegreya Sans", sans-serif !important; }

@media only screen and (max-width: 480px) {
  .toastify {
    text-align: center; } }

@media only screen and (max-width: 414px) {
  h1 {
    font-size: 2rem; }
  h2 {
    font-size: 1.5rem; } }

#home {
  color: #fff; }
  #home .container {
    display: flex;
    flex-direction: column;
    align-items: center; }
  #home img {
    margin-top: 5rem;
    width: 300px;
    height: 300px;
    filter: drop-shadow(0 0 200px rgba(255, 255, 255, 0.5));
    -webkit-filter: drop-shadow(0 0 200px rgba(255, 255, 255, 0.5));
    -moz-filter: drop-shadow(0 0 200px rgba(255, 255, 255, 0.5)); }
  #home .social {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start; }
    #home .social a {
      display: flex;
      align-items: flex-start;
      text-decoration: none; }
      #home .social a:not(:nth-child(1)) {
        margin-top: -3px; }
    #home .social i {
      color: #f0938c;
      font-size: 3rem;
      margin: 20px;
      transition: 0.3s;
      -webkit-transform: translate3d(0, 0, 0); }
      #home .social i:hover {
        color: #fff;
        transition: 0.3s;
        -webkit-transform: translate3d(0, 0, 0); }

@media only screen and (max-width: 414px) {
  #home img {
    width: 200px;
    height: 200px;
    filter: drop-shadow(0 0 200px rgba(255, 255, 255, 0.2));
    -webkit-filter: drop-shadow(0 0 200px rgba(255, 255, 255, 0.2));
    -moz-filter: drop-shadow(0 0 200px rgba(255, 255, 255, 0.2)); } }

#not-found .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

#not-found h1 {
  margin-bottom: 2rem; }

